<template>
	<div class="component-tree-node">
		<div class="node-content">
			<div
				:class="{
					expand: true,
					leaf: isLeaf,
					expanded: isExpand && !isLeaf,
				}"
				v-on:mousedown="handleExpand"> › </div>
			<div class="label"
				v-on:mousedown="selectNode(nodeData)">{{ getLabel(nodeData) }}</div>
		</div>
		<div class="node-children"
			v-if="isExpand && !isLeaf"
			ref="children">
			<c-tree-node
				v-for="(child, index) in children"
				:key="index"
				:nodeData="child"
				:defaultExpand="defaultExpand"
				/>
		</div>

	</div>
</template>

<script>

	export default {
		name: 'c-tree-node',
		inject: ['getChildren', 'getLabel', 'selectNode', 'defaultExpand'],
		props: {
			nodeData: {
				type: [Object, Array, String, Number, Boolean],
			}
		},
		data(){
			let children = this.getChildren(this.nodeData);
			return {
				children,
				isLeaf: Array.isArray(children) ? children.length === 0 : true,
				isExpand: this.defaultExpand
			}
		},
		methods: {
			handleExpand(){
				this.isExpand = !this.isExpand;
			},
		}
	}
</script>
