<template>
	<div class="component-tree">
		<c-tree-node
			v-for="(children, index) in data"
			:key="index"
			:defaultExpand="defaultExpand"
			:nodeData="children"
			/>
	</div>
</template>

<script>
	import treeNode from './tree_node.vue';

	export default {
		components: {
			'c-tree-node': treeNode,
		},
		props: {
			data: {
				type: Array,
			},
			label: {
				type: [String, Function],
				default: 'label',
			},
			children: {
				type: [String, Function],
				default: 'children',
			},
			defaultExpand: {
				type: Boolean,
				default: true,
			},
		},
		provide(){
			return {
				selectNode: this.selectNode,
				getChildren: this.getChildren,
				getLabel: this.getLabel,
				defaultExpand: this.defaultExpand,
			}
		},
		methods: {
			getChildren(node){
				if( typeof this.children === 'string' ){
					return node[this.children];
				}else if( typeof this.children === 'function' ){
					return this.children(node);
				}
			},
			getLabel(node){
				if( typeof this.label === 'string' ){
					return node[this.label];
				}else if( typeof this.label === 'function' ){
					return this.label(node);
				}
			},
			selectNode(node){
				this.$emit('selectnode', node);
			},

		},
	}
</script>

<style lang="less">
	.component-tree{
		width: fit-content;
		min-width: 100%;
		max-height: 100%;
		height: fit-content;
		min-height: 100%;
